import React from "react";
import { useState } from "react";
import styles from "../css/Home.module.scss";
import "../css/transitions.scss";
import pic from "../Media/circle-cropped.png";
import { CSSTransition } from "react-transition-group";
const Firstcomponent = ({ setpage }) => {
  const [show, setshow] = useState(true);
  return (
    <div className={styles.home}>
      <div className={styles.container1}>
        <h1>Nathan Yik</h1>
        <p className={styles.description}>Software Engineer | Student</p>
        <div className={styles.link}>
          <a
            href="https://www.linkedin.com/in/nathan-yik-648666187/"
            target="_blank"
            className={styles.link}
          >
            LinkedIn{" "}
          </a>
          <a
            href="https://github.com/Pawnnub"
            target="_blank"
            className={styles.link}
          >
            GitHub{" "}
          </a>
        </div>
      </div>
      <div className={styles.container2}>
        <img src={pic} className={styles.img} />
      </div>
    </div>
  );
};

export default Firstcomponent;
